
@import url("https://fonts.googleapis.com/css?family=Proza+Libre");
#team-name{font-family:'Libre Baskerville', serif;color:#1158a8;font-size:70px;animation-duration:2s;animation-delay:1s
}
.team-quote{font-family:'Noto Sans TC', serif;color:#53b5b4;animation-duration:2s;animation-delay:2s;font-weight:200;line-height:44px;padding:5%
}
#HDataPred{display:-ms-flexbox;display:flex;background-color:#1158a8
}
#HDataPred .row{display:-ms-flexbox;display:flex
}
#HDataPred .row .col-sm-12 button{margin-top:2vh;border-color:white
}
#HDataPred .row .col-sm-12 button:hover{background-color:transparent;border-color:#f6c555
}
#HDataPred .row .col-sm-12 button:hover a{color:#f6c555
}
#HDataPred .row .col-sm-12 button:focus{background-color:transparent
}
#HDataPred .row .col-sm-12 button a{color:white;text-decoration:none
}
.col-sm-6{display:-ms-flexbox;display:flex
}
#HGeneSol{display:-ms-flexbox;display:flex;background-color:#65bdbc
}
#HGeneSol .row{display:-ms-flexbox;display:flex
}
#HGeneSol .row .col-sm-12 button{margin-top:2vh;color:white;border-color:white
}
#HGeneSol .row .col-sm-12 button:hover{background-color:transparent;color:#f6c555;border-color:#f6c555
}
.background{background-size:cover;background-position:center;height:300px;opacity:1;z-index:0;width:300px;border-radius:50%;transition:opacity 0.8s;margin:auto;border:3px solid white;margin-top:5vh
}
@media screen and (max-width: 576px){
.background{width:250px;height:250px
}
}
#HDataPred .background{background-image:url("../assets/gears.jpg")
}
#HGeneSol .background{background-image:url("../assets/DNA.jpg")
}
.product-name{font-family:'Proza Libre', serif;color:white;transition:all 0.8s ease;width:80%;margin:auto;z-index:2;padding:5vh 0
}
.product-desc{font-family:'Noto Sans TC', serif;font-weight:200;height:140px
}
#HDataPred .background:hover+.product-name{opacity:1
}
#HGeneSol .background:hover+.product-name{opacity:1
}
@media screen and (max-width: 576px){
.display-4{font-size:2.6rem
}
}



