
@import url(http://fonts.googleapis.com/earlyaccess/notosanstc.css);
#metology-hero .container{padding-top:6%
}
#metology-hero .container h1{padding-bottom:2%
}
#metology-hero .container h3{padding-bottom:2%
}
#metology-hero .container .card{box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);margin:auto;width:70%;height:480px;display:-ms-flexbox;display:flex
}
@media screen and (max-width: 576px){
#metology-hero .container .card{width:100%;height:300px
}
}
#metology-hero .container .card iframe{margin:auto;width:98%;height:98%
}
.background-patch{background-image:url("../assets/pattern6.png");height:100%;width:100%;background-color:white;position:absolute;top:0px;z-index:-1
}



