
.modal-base{z-index:10;position:fixed;height:100%;width:100%;background-color:rgba(0,0,0,0.5);top:0;display:-ms-flexbox;display:flex
}
.modal-base .modal{margin:auto;width:80%;height:80%;background-color:white;border-radius:20px;display:block;position:relative
}
.modal-base .modal .close-result{position:absolute;right:30px;top:10px;font-size:30px;z-index:12;transition:color 0.3s ease
}
.modal-base .modal .close-result:hover{color:#e0e0e0
}
.modal-base .modal .result-wrap{padding-top:40px
}
.modal-base .modal .result-wrap h3{padding-bottom:10px
}
.modal-base .modal .suggestion-wrap{padding-top:40px
}
.modal-base .modal .suggestion-wrap h3{padding-bottom:10px
}
.risk-text{padding-top:10px
}



